

.title {
    text-align: center;
    font-weight: 700;
    font-size: 25px;
    color: #000000;
    margin-left: auto;
    margin-right: auto;
}

.item {
    display: flex;
        width: 300px;
        height: 60px;
        margin-bottom: 10px;
        margin-right: auto;
        margin-left: auto;
        padding-left: 20px;
        justify-content: left;
        border: 2px solid gray;
        border-radius: 4px;
        box-shadow: 5px 5px 19px -1px #000000;
        background-color: rgb(187, 181, 181);
}

.box {
    display: flex;
        flex-wrap: nowrap;
        align-items: center;
        justify-content: left;
}

.status_on {
    display: block;
        margin-right: 0;
        margin-right: 30px;
        margin-bottom: 35px;
        align-content: flex-start;
        width: 15px;
            height: 15px;
            border-radius: 50%;
            background-color: green;
}

.status_off {
    display: block;
    margin-right: 0;
    margin-right: 30px;
    margin-bottom: 35px;
    align-content: flex-start;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: red;
}

.avatar {
    display: inline;
        margin-right: 20px;
        padding: 0;
}

.name {
    margin: 0;
        font-size: 25px;
        font-weight: 600;
}